import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';

import { environment } from "@env/environment";
import { User } from "@model/user";
import { Role } from '@app/enum/app_enum';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private baseUrl = environment.apiUrl;
  constructor(private http: HttpClient) { }

  userLists(searchData: String, page: Number, limt: Number, sortColumn: String, SortDirection: String, role: String) {
    let url: any;
    if (searchData) url = this.baseUrl + "/users" + "?userType=" + role + "&search=" + searchData;
    else
      url =
        this.baseUrl +
        "/users" + "?userType=" + role + "&page=" +
        page +
        "&limit=" +
        limt +
        "&sortColumn=" +
        sortColumn +
        "&sortDirection=" +
        SortDirection;

    return this.http.get<any>(url).pipe(map((obj) => obj || []));
  }

  createUser(user: User) {
    let url = this.baseUrl + "/users/" + user.userType;
    var formData: any = new FormData();
    formData.append("username", user.username);
    formData.append("email", user.email);
    formData.append("gender", user.gender);
    formData.append("file", user.file);
    formData.append("dateOfBirth", user.dateOfBirth);
    formData.append("password", user.password);
    if (user.userType == Role.Trainer) {
      formData.append("techanics", user.techanics);
      formData.append("height", user.height);
      formData.append("weight", user.weight);
    } else
      formData.append("address", user.address);


    return this.http.post(url, formData)
  }

  updateUser(user: User) {
    let url = this.baseUrl + "/users/" + user.userType + "/" + user._id;
    var formData: any = new FormData();
    formData.append("username", user.username);
    formData.append("email", user.email);
    formData.append("role", user.userType);
    formData.append("gender", user.gender);
    formData.append("file", user.file);
    formData.append("dateOfBirth", user.dateOfBirth);
    formData.append("password", user.password);
    if (user.userType == Role.Trainer) {
      formData.append("techanics", user.techanics);
      formData.append("height", user.height);
      formData.append("weight", user.weight);
      formData.append("address", user.address);
    } else
      formData.append("address", user.address);
    return this.http.put(url, formData)
  }



  getUserDetail(userId: String, routeRole: String) {
    let url = this.baseUrl + "/users/" +userId;
    return this.http.get<any>(url).pipe(map((obj) => obj || {}));
  }
}
